import {createRoot} from "react-dom/client";

const
	React = require('react');

/**
 * Static values
 */
let layer,
    currentMenu,
    contextmenuListener;

/**
 * Find all dom node in the element parent hierarchy
 * @param element
 * @returns {Array} An array of dom node generated by the React ContextMenu component
 */
export function findAllMenuFrom( element ) {
	let menus = [];
	do {
		menus.push(...Array.from(element.children).filter(node => node.classList.contains("inContextMenuComp")))
		element = element.parentNode;
	} while ( element && element !== document );
	return menus;
}


/**
 * Apply 'id' css anim on 'node' element during 'tm'ms
 * @param node
 * @param id
 * @param tm
 * @param cb
 */
export function applyCssAnim( node, id, tm, cb ) {
	tm      = tm || 500;
	let stm,
	    evt = ( e ) => {
		    if ( e && e.target !== node ) {
			    return;
		    }
		    clearTimeout(stm);
		    Object.assign(node.style, { animation: null });
		    
		    node.removeEventListener('animationend', evt);
		    cb && cb(node);
	    };
	node.addEventListener('animationend', evt);
	
	Object.assign(node.style, { animation: id + " " + (tm / 1000) + "s forwards" });
	
	stm = setTimeout(evt, tm * 1.5);
}

/**
 * Remove all listeners (destroy the context Menu
 */
export function clearContextListeners( ContextMenu ) {
	try {
		document.body.removeChild(layer);
		document.removeEventListener(
			ContextMenu.DefaultMenuEvent,
			contextmenuListener);
		contextmenuListener = layer = null;
	} catch ( e ) {
		
	}
}

const ContextMenuComp = ( { menuComps, onMenuItemRendered, setMenusToClean, DefaultMenuComp, clickEvent } ) => {
	const airRenderRef = React.useRef();
	React.useEffect(
		() => {
			setMenusToClean(menuComps.map(
				                cmp => {
					                let cmpMenuNode = document.createElement('div');
					                
					                cmpMenuNode.className  = "inContextSubMenu";
					                cmp.onMenuItemRendered = onMenuItemRendered;//
					                if ( airRenderRef.current?.parentNode )
						                airRenderRef.current.parentNode.appendChild(cmpMenuNode);
					                return cmp.triggerRender(cmpMenuNode, menuComps, clickEvent);
				                }
			                )
			)
		}
	)
	return <DefaultMenuComp>
		<div ref={airRenderRef} style={{ display: "none" }}/>
	</DefaultMenuComp>
}

/**
 * Init the context Menu
 * @param ContextMenu
 */
export function initContextListeners( ContextMenu, menusById ) {
	
	// init overlay
	layer = document.createElement("div");
	Object.assign(layer.style, {
		pointerEvents: "none",
		position     : "fixed",
		width        : "100%",
		height       : "100%",
		top          : "0",
		left         : "0",
		zIndex       : ContextMenu.DefaultZIndex,
		display      : 'none'
	});
	layer.className = "inContextMenuLayer";
	document.body.appendChild(layer);
	
	let resize, scroll, menusToClean, reactRoot,
	    destroy = ( e, now ) => {
		    let clear = tm => {
			    let crRoot          = reactRoot;
			    layer.style.display = 'none';
			    currentMenu         = null;
			    while ( menusToClean?.length )
				    menusToClean.pop()()
			    layer.innerHTML = '';
			    setTimeout(
				    () => {
					    
					    crRoot.unmount();
				    }, 200
			    )
		    };
		    window.removeEventListener('resize', resize);
		    window.removeEventListener('scroll', scroll);
		    document.body.removeEventListener('click', destroy)
		    if ( !now ) {
			    if ( ContextMenu.DefaultHideAnim )
				    applyCssAnim(currentMenu, ContextMenu.DefaultHideAnim, ContextMenu.DefaultAnimDuration, clear);
			    else setTimeout(clear, 10);
		    }
		    else clear();
		    
	    };
	
	// on right click
	document.addEventListener(
		ContextMenu.DefaultMenuEvent,
		contextmenuListener = function ( e ) {
			
			if ( !ContextMenu.shouldUseContextMenu(e) )
				return;
			
			
			if ( currentMenu ) // if there an open menu
				destroy(null, true);
			
			let rootExclusive,
			    menuComps = findAllMenuFrom(e.target)
				    .reduce(
					    ( list, node ) => {
						    let cmp = node.menuId && menusById[node.menuId];
						    if ( !cmp || rootExclusive ) return list;
						    list.push(cmp);
						    if ( cmp.props?.hasOwnProperty("root") )
							    rootExclusive = cmp;
						    return list
					    },
					    []
				    ),
			    x, y,
			    mw        = window.innerWidth,
			    mh        = window.innerHeight;
			
			if ( !menuComps.length || menuComps[0].props.hasOwnProperty('native') )
				return;
			
			// ok we deal with this event
			e.preventDefault();
			e.stopPropagation();
			
			document.body.addEventListener('click', destroy);
			
			layer.style.display = 'block';
			
			window.addEventListener(
				'resize',
				resize = () => {
					destroy(null, false);
				});
			window.addEventListener(
				'scroll',
				scroll = () => {
					destroy(null, false);
				});
			let DefaultMenuComp   = ContextMenu.DefaultMenuComp;
			currentMenu           = document.createElement('div');
			currentMenu.className = "inContextMenu";
			layer.appendChild(currentMenu)
			reactRoot = createRoot(currentMenu)
			
			// Must wait the render of JitComp, then the render of all the submenus
			let waitingRenders     = menuComps.length,
			    onMenuItemRendered = () => {
				    if ( !--waitingRenders )
					    // show on next animaton frame
					    requestAnimationFrame(
						    function () {
							    
							    let orient = ["Bottom", "Left"]
							    
							    x = e.x;
							    y = e.y;
							    
							    
							    if ( (x + currentMenu.offsetWidth) > mw ) {
								    x -= currentMenu.offsetWidth;
								    orient[1] = "Right";
							    }
							    if ( (y + currentMenu.offsetHeight) > mh ) {
								    y -= currentMenu.offsetHeight;
								    orient[0] = "Top";
							    }
							    currentMenu.classList.add("inContextMenuDir_" + orient.join(""));
							    Object.assign(
								    currentMenu.style,
								    {
									    top       : y + 'px',
									    left      : x + 'px',
									    width     : currentMenu.offsetWidth + 'px',
									    height    : currentMenu.offsetHeight + 'px',
									    visibility: 'visible'
								    }
							    );
							    ContextMenu.DefaultShowAnim &&
							    applyCssAnim(currentMenu, ContextMenu.DefaultShowAnim, ContextMenu.DefaultAnimDuration)
						    }
					    );
			    };
			reactRoot.render(<ContextMenuComp {...{
				menuComps,
				onMenuItemRendered,
				setMenusToClean: ( list ) => (menusToClean = list),
				DefaultMenuComp,
				clickEvent     : e
			}}/>)
			
			Object.assign(
				currentMenu.style,
				{
					pointerEvents: "all",
					position     : "absolute",
					display      : "flex",
					visibility   : 'hidden'
				}
			);
			
			
			return false;
		});
}
